@import url(https://fonts.googleapis.com/css?family=Lato|Open+Sans);
.overall {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: row;
            flex-direction: row;
}

.breadcrumb {
    margin-top: 24px;
    margin-left: 27.8px;
    list-style: none;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
}

.list-item {
    display: inline;
    font-size: 24px;
}

.breadcrumb .list-item+.list-item:before {
    padding: 8px;
    content: "/\A0";
}

.links:nth-last-child(1) {
    color: #000;
    text-decoration: none;
}

.links {
    text-decoration: none;
    color: #81878C;
}



.fallback {
    display: grid;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: center;
            justify-content: center;
    width: 100%;
    font-size: 24px;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
    letter-spacing: 1px;
}
.content {
    float: right;
    width: calc(100% - 256px);
    background: #fff;
    height: 100vh;
}
.font-size-12 {
    font-family: "Lato", sans-serif;
    font-size: 24px;
    color: #AFB2B6;
    font-size: 12px;
    letter-spacing: 0;
    font-weight: bold;
    padding-left: 32px;
    padding-top: 32px;
    padding-bottom: 15px;
}

.sidebar {
    width: 256px;
    float: left;
    background: #F9FAFC;
    height: 100vh;
}

.sidebar-menu {
    width: 100%;
}

.sidebar-item {
    height: 43px;
    font-size: 16px;
    color: #2F363F;
    font-family: 'Lato', sans-serif;
    clear: both;
}

.inner-item {
    color: #535B62;
    clear: both;
}

.sidebar-item:active {
    background-color: #EEEFF1;
}

.sidebar-item-text {
    float: left;
    padding-left: 32px;
    padding-top: 12px;
}

.sidebar-item-inner-text {
    font-size: 16px;
    font-family: inherit;
    padding-left: 16px;
    padding-top: 12px;
    float: left;
}

.sidebar-item-extend {
    float: right;
    background-color: inherit;
    border: none;
    border-radius: 50%;
    width: 24px;
    height: 24px;
    margin-top: 9px;
    margin-right: 24px;
}

.vert-line {
    width: 2px;
    height: 16px;
    margin-top: 15px;
    margin-left: 32px;
    background: #EEEFF1;
    float: left;
}

.sidebar-item-extend:hover {
    cursor: pointer;
}

.sidebar-item-extend:focus {
    outline: 0;
}

.sidebar-item-extend .clicked {    /* TODO: Doesn't work as intended */
    -webkit-transform: rotate(180deg);
            transform: rotate(180deg);
    -webkit-transition-delay: 500ms;
            transition-delay: 500ms;
}
/* Also have to use Helvetica Neue */
.mainContent {
    width: 100%;
    position: relative;
}
